var chatState = {
    messagelist: [],
    clientInfo: {},//顾问信息
    socket: null,//socket
}

export default function chat(state = chatState, action) {
    switch (action.type) {
        // 设置聊天信息
        case 'SETMESSAGELIST':
            return {
                ...state,
                messagelist: action.value
            }
        case 'PUSHMESSAGE':
            var msglist = state.messagelist
            msglist = msglist.concat(action.value)
            return {
                ...state,
                messagelist: msglist
            }
        case 'SETCLIENTINFO':
            return {
                ...state,
                clientInfo: action.value,
            };
        case 'SETSOCKET':
            return {
                ...state,
                socket: action.value,
            };
        default:
            return state
    }
}